<template>
  <div class="technology">
    <h2>{{ techArticle.title }}</h2>
    <div v-html="techArticle.content"></div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Technology",
  data(){
    return {
      techArticle:{}
    }
  },
  created() {
    axios.get('/TechArticles/'+this.$route.params.id)
      .then((data) =>{
        console.log(data.data)
        this.techArticle = data.data
      })
    // axios.post('/TechArticles',{
    //   title:'test',
    //   content:'testtest123123',
    //   categories:['js'],
    //   author:'zhj'
    // }).then(function (data){
    //   console.log(data);
    // })
  }
}
</script>

<style scoped>
.technology h2 {
  padding: 15px 0;
}
.technology >>> h3 {
  padding: 10px 0;
}
.technology >>> p {
  text-indent: 2em;
}
</style>



